
.player .normalPlayer {
  position: fixed;
  top: 0px;
  left: 0px;
  bottom: 0px;
  right: 0px;
  background: #222;
  z-index: 150;
}
.player .normalPlayer.normal-enter-active,
.player .normalPlayer.normal-leave-active {
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
}
.player .normalPlayer.normal-enter-active .top,
.player .normalPlayer.normal-leave-active .top,
.player .normalPlayer.normal-enter-active .bottom,
.player .normalPlayer.normal-leave-active .bottom {
  -webkit-transition: all 0.3s cubic-bezier(0.86, 0.18, 0.82, 1.32);
  transition: all 0.3s cubic-bezier(0.86, 0.18, 0.82, 1.32);
}
.player .normalPlayer.normal-enter,
.player .normalPlayer.normal-leave-to {
  opacity: 0;
}
.player .normalPlayer.normal-enter .top,
.player .normalPlayer.normal-leave-to .top {
  -webkit-transform: translate3d(0, -100px, 0);
          transform: translate3d(0, -100px, 0);
}
.player .normalPlayer.normal-enter .bottom,
.player .normalPlayer.normal-leave-to .bottom {
  -webkit-transform: translate3d(0, 100px, 0);
          transform: translate3d(0, 100px, 0);
}
.player .normalPlayer .background {
