
@import "../../common/stylus/icon.css";
.search {
  width: 100%;
  height: 30px;
}
.search .icon_wrapper {
  color: #c9c9cd;
  display: inline-block;
  vertical-align: top;
  width: 30px;
  height: 100%;
}
.search .icon_wrapper .icon-music_search {
  font-size: 20px;
}
.search .input_wrapper {
  display: inline-block;
  vertical-align: top;
  width: 80px;
  height: 100%;
}
.search .input_wrapper input {
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
  height: 20px;
  width: 100px;
  border-radius: 20px;
  border: 1px solid #c9c9cd;
  outline: none;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  padding-left: 1em;
}
.search .input_wrapper input::-webkit-search-cancel-button {
  display: none;
}


