
.loader-wrapper {
  position: fixed;
  left: 0px;
  top: 0px;
  width: 100%;
  height: 100%;
  z-index: 50;
  background-color: #1f1e1d;
}
.loader-wrapper.fade-enter-active,
.loader-wrapper.fade-leave-active {
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
}
.loader-wrapper.fade-enter,
.loader-wrapper.fade-leave-to {
  opacity: 0;
}
.loader-wrapper .wave {
  position: relative;
  width: 100%;
  height: 100%;
  background-color: #1ee494;
  overflow: hidden;
}
.loader-wrapper .wave::before,
.loader-wrapper .wave::after {
  content: "";
  position: absolute;
  width: 750px;
  height: 750px;
  top: -600px;
  left: -50%;
  background-color: rgba(31,30,29,0.4);
  border-radius: 45%;
  -webkit-transform: translate(-50%, -70%) rotate(0);
          transform: translate(-50%, -70%) rotate(0);
  -webkit-animation: rotate 6s linear -5s infinite;
          animation: rotate 6s linear -5s infinite;
  z-index: 10;
}
.loader-wrapper .wave::after {
  border-radius: 47%;
  background-color: rgba(31,30,29,0.9);
  -webkit-transform: translate(-50%, -70%) rotate(0);
          transform: translate(-50%, -70%) rotate(0);
  -webkit-animation: rotate 10s linear -5s infinite;
          animation: rotate 10s linear -5s infinite;
  z-index: 20;
}
.loader-wrapper .floor {
  position: absolute;
  bottom: 0px;
  left: 0px;
  width: 100%;
  height: 12%;
  background-color: #fff;
}
.loader-wrapper .floor .text-wrapper {
  width: 100%;
  height: 100%;
  padding-left: 20%;
  padding-top: 10px;
}
.loader-wrapper .floor .text-wrapper .name {
  display: inline-block;
  width: 40px;
  height: 40px;
  padding: 6px;
  border-radius: 6px;
  background-color: #1ee494;
  font-family: "Meiryo UI";
  font-size: 40px;
  margin-right: 20px;
}
.loader-wrapper .floor .text-wrapper .say {
  color: #c0c0c0;
  font-size: 18px;
}
@-webkit-keyframes rotate {
50% {
    -webkit-transform: translate(-50%, -73%) rotate(180deg);
            transform: translate(-50%, -73%) rotate(180deg);
}
100% {
    -webkit-transform: translate(-50%, -70%) rotate(360deg);
            transform: translate(-50%, -70%) rotate(360deg);
}
}
@keyframes rotate {
50% {
    -webkit-transform: translate(-50%, -73%) rotate(180deg);
            transform: translate(-50%, -73%) rotate(180deg);
}
100% {
    -webkit-transform: translate(-50%, -70%) rotate(360deg);
            transform: translate(-50%, -70%) rotate(360deg);
}
}


