body,
html {
  line-height: 1;
  font-family: 'Dosis', 'Source Sans Pro', 'Helvetica Neue', Arial, sans-serif;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  -webkit-tap-highlight-color: transparent;
  background: #fff;
  color: #fff;
}
.music-list {
  position: fixed;
  z-index: 100;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background-color: #fff;
}
.music-list .back {
  position: absolute;
  left: 6px;
  top: 0px;
  z-index: 50;
}
.music-list .back .icon-back {
  display: block;
  padding: 10px;
  font-size: 22px;
  color: #fff;
}
.music-list .title {
  position: absolute;
  top: 0;
  left: 10%;
  z-index: 40;
  width: 80%;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  text-align: center;
  line-height: 40px;
  font-size: 18px;
  color: #fff;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}
.music-list .bg-image {
  position: relative;
  width: 100%;
  height: 0px;
  padding-top: 70%;
  background-size: cover;
  -webkit-transform-origin: top;
          transform-origin: top;
}
.music-list .bg-image .filter {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  background: rgba(7,27,20,0.4);
}
.music-list .bg-layer {
  position: relative;
  height: 100%;
  width: 100%;
  background: #fff;
}
.music-list .list {
  position: fixed;
  top: 0;
  bottom: 0;
  width: 100%;
  background: #fff;
}
.music-list .list .song-list-wrapper {
  padding: 20px 0 30px 20px;
}
.music-list .list .song-list-wrapper .song-item {
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  height: 44px;
  font-size: 14px;
  margin-bottom: 20px;
  position: relative;
}
.music-list .list .song-list-wrapper .song-item::after {
  display: block;
  position: absolute;
  left: 0px;
  bottom: 0px;
  width: 100%;
  background-color: #dee6e3;
  height: 1px;
  content: '';
}
.music-list .list .song-list-wrapper .song-item .name {
  color: #323534;
}
.music-list .list .song-list-wrapper .song-item .desc {
  color: #696d6a;
  margin-top: 4px;
}


