
@import "../../common/stylus/quill.snow.css";
@import "~highlight.js/styles/monokai-sublime.css";
@import "../../common/stylus/icon.css";
.article {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 50;
  background-color: #fff;
  padding-top: 41px;
}
.article.slide-enter-active,
.article.slide-leave-active {
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}
.article.slide-enter,
.article.slide-leave-to {
  -webkit-transform: translate3d(100%, 0, 0);
          transform: translate3d(100%, 0, 0);
}
.article .article-head-wrapper {
  position: fixed;
  top: 0;
  left: 0;
  height: 41px;
  width: 100%;
  background-color: #fff;
  z-index: 100;
}
.article .article-head-wrapper .head {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  height: 100%;
  width: 100%;
  position: relative;
}
.article .article-head-wrapper .head::after {
  display: block;
  position: absolute;
  left: 0px;
  bottom: 0px;
  width: 100%;
  background-color: #d0d0d0;
  height: 1px;
  content: '';
}
.article .article-head-wrapper .head .back {
  -webkit-box-flex: 0;
      -ms-flex: 0 0 41px;
          flex: 0 0 41px;
  height: 100%;
  padding: 8px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  text-align: center;
}
.article .article-head-wrapper .head .back .icon-back {
  font-size: 24px;
  width: 24px;
  height: 24px;
  line-height: 24px;
  color: #d0d0d0;
}
.article .article-head-wrapper .head .mini-title {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  height: 100%;
  font-size: 12px;
  line-height: 41px;
  text-align: center;
  padding: 0 30px 0 30px;
  color: #000;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}
.article .article-head-wrapper .head .mini-title .text.fade-enter-active,
.article .article-head-wrapper .head .mini-title .text.fade-leave-active {
  -webkit-transition: all 0.4s;
  transition: all 0.4s;
}
.article .article-head-wrapper .head .mini-title .text.fade-enter,
.article .article-head-wrapper .head .mini-title .text.fade-leave-to {
  opacity: 0;
}
.article .article-head-wrapper .head .menu {
  -webkit-box-flex: 0;
      -ms-flex: 0 0 41px;
          flex: 0 0 41px;
  height: 100%;
}
.article .article-title {
  padding: 8px;
  background-color: #fff;
  border-bottom: 5px solid #e0e0e0;
  position: relative;
  z-index: 50;
}
.article .article-title .title {
  font-size: 16px;
  font-weight: 600;
  text-align: center;
  color: #000;
}
.article .article-content {
  width: 100%;
  height: 100%;
  padding: 20px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  background-color: #fff;
}
.article .article-content .content {
  width: 100%;
  height: 100%;
  color: #000;
}
.article .article-content .content .content-text {
  background-color: #fff;
}
.article .article-content .content .content-text img {
  width: 100%;
}


